











import { Component, Vue, Prop, Watch } from "vue-property-decorator";
// @ts-ignore
import * as $ from "jquery";

@Component
export default class ProgressModal extends Vue {

    @Prop()
    open!: boolean;

    @Prop()
    progress!: number;

    mounted() {
        $(this.$el).modal("hide");
    }

    @Watch("open")
    onOpenChange() {
        if (this.open) {
            $(this.$el).modal("show");
        } else {
            setTimeout(() => $(this.$el).modal("hide"), 500);
        }
    }

}
